import { NoChildrenProp } from '../../types';

import { Bias, LatLng } from './types';

export default interface BaseLocationProps extends NoChildrenProp {
  /**
   * Location in either text format (name or address) or coordinates.
   * @default 'current'
   */
  location?: LatLng | string | 'current';
  /**
   * Flag to control 'disabled' state for input.
   * @default false
   */
  disabled?: boolean;
  /** Callback fired when an error occurs. This function gets called with one argument of type Error. */
  onError?: (error: Error) => void;
  /** Biasing query results towards user location/preference. */
  bias?: Bias;
}
